.container {
  height   : auto;
  overflow : hidden;
  position : relative;
  width    : 100%;
}

#post {
  display  : block;
  margin   : 0 auto;
  width    : 1420px;
  height   : auto;
  overflow : hidden;
  z-index  : 10;
  @media only screen and (min-width : 960px) and (max-width : 1419px) {
    width : 960px;
  }
  @media only screen and (min-width : 768px) and (max-width : 959px) {
    width : 740px;
  }
  @media only screen and (min-width : 480px) and (max-width : 767px) {
    width : 440px;
  }
  @media only screen and (max-width : 479px) {
    width : 300px;
  }
  article {
    position : relative;
    time {
      display : inline-block;
    }
    .format-aside {
      .post-title {
        a::before {
          display : inline-block;
          content : '@';
        }
      }
    }
  }
}

.list--view {
  img {
    display : none;
  }
  .list--post {
    border-left   : 2px solid transparent;
    border-bottom : 1px solid #ddd;
    line-height   : 1.71428571;
    padding       : 10px 0;
    &:last-child {
      border-bottom : 0;
    }
    .wrap-content {
      font-size : @f-large-size;
      .grid {
        display : none;
      }
    }
  }
  a.link {
    cursor    : pointer;
    color     : #424242;
    display   : block;
    font-size : @f-large-size;
    padding   : 10px 5px;
    .transition(all 0.2s ease-in-out);
    &:hover {
      padding-left : 15px;
    }
    h4 {
      display        : inline-block;
      margin         : 0 10px 0 0;
      text-transform : none;
    }
    time {
      &:extend(.list--view a.link h4);
      &::before {
        content      : "|";
        margin-right : 10px;
      }
    }
  }
}

.grid--view {
  .post--item {
    background   : none repeat scroll 0 0 #fcfcfc;
    border-image : none;
    border       : 1px solid @gray-light;
    float        : left;
    margin       : 0px 10px 30px;
    width        : 440px;
    height       : auto;
    .transition(opacity .3s ease-in-out);
    @media only screen and (min-width : 768px) and (max-width : 959px) {
      margin : 0 4px 20px;
      width  : 360px;
    }
    @media only screen and (min-width : 480px) and (max-width : 767px) {
      margin : 0 0 20px;
      width  : 440px;
    }
    @media only screen and (max-width : 479px) {
      margin : 0 0 20px;
      width  : 300px;
    }
    .wrap-content {
      height  : auto;
      z-index : 10;
      &.format-status {
        .grid-icon(@i-grid-icon-3, #742e34);
        .post-meta {
          & > div {
            width : 50%;
          }
        }
      }
      &.format-gallery {
        .grid-icon(@i-grid-icon-4, #4a6e68);
        .post-meta {
          & > div {
            width : 25%;
          }
        }
      }
      &.format-aside {
        .grid-icon(@i-grid-icon-5, #5c4942);
        .post-meta {
          & > div {
            width : 50%;
          }
        }
      }
      &.format-link {
        .grid-icon(@i-grid-icon-2, #51492d);
        .post-thumb {
          border-top : 1px solid #e5e5e5;
        }
        .post-meta {
          & > div {
            width : 25%;
          }
        }
      }
      &.format-quote {
        .grid-icon(@i-grid-icon-1, #094a40);
        .post-quote {
          border        : 1px solid rgba(0, 0, 0, 0.05);
          margin        : 0 5px 15px;
          padding       : 0 15px 0 15px;
          position      : relative;
          text-overflow : ellipsis;
          z-index       : 11;
          p {
            font-size     : 15px;
            margin-bottom : 0px;
            max-height    : 157px;
          }
          span {
            display       : block;
            text-align    : right;
            padding-top   : 9px;
            margin-bottom : 16px;
            a {
              color         : #999;
              display       : block;
              font-size     : @f-small-size;
              line-height   : 1.4;
              overflow      : hidden;
              padding-top   : 1px;
              text-overflow : ellipsis;
              white-space   : nowrap;
              &:hover {
                text-decoration : underline;
              }
            }
          }
        }
        .post-meta {
          & > div {
            width : 33.333%;
          }
        }
      }
      p {
        display : block;
        margin  : 1em 0;
        padding : 0;
      }
      .heading {
        margin-bottom : 20px;
        padding       : 0 20px;
        position      : relative;
        .avatar-icon {
          position : relative;
          float    : left;
          margin   : 20px 15px 0 0;
          padding  : 0;
          height   : 46px;
          width    : 46px;
          .front {
            background-image : url("../images/icons_rika.png");
            width            : 100%;
            height           : 100%;
            display          : inline-block;
            text-indent      : -9999px;
            .transition(all .8s ease-in-out);
          }
          &:hover {
            .back {
              .rotateY(0deg);
            }
            .avatar,
            .front {
              .rotateY(180deg);
            }
          }
          .avatar,
          .front,
          .back {
            backface-visibility         : hidden;
            -webkit-backface-visibility : hidden;
            position                    : absolute;
            top                         : 0;
            left                        : 0;
          }
          .back {
            width  : 100%;
            height : 100%;
            .rotateY(180deg);
            .transition(all .8s ease-in-out);
            a {
              background-color : @brand-primary;
              border-radius    : 50%;
              color            : @white-color;
              display          : block;
              height           : 100%;
              line-height      : 46px;
              text-align       : center;
              text-shadow      : 1px 1px rgba(0, 0, 0, 0.2);
              vertical-align   : middle;
              width            : 100%;
              .transition(all .3s ease);
            }
          }
          .avatar {
            border-radius : 50%;
            .transition(all .8s ease-in-out);
          }
        }
        .post-title {
          margin        : 0 0 15px 61px;
          padding       : 23px 0 0;
          text-overflow : ellipsis;
          .title-wrap {
            .title {
              border       : 0 none;
              font-size    : @f-large-size;
              font-weight  : normal;
              margin       : 0;
              unicode-bidi : normal;
              @media only screen and (max-width : 959px) {
                font-size : 15px;
              }
              a {
                .transition(all .2s ease-in-out);
                &:hover {
                  color : #ba89b6;
                }
              }
            }
            time {
              color     : #999;
              font-size : @f-small-size;
            }
          }
        }
        .tag {
          ul {
            font-size  : @f-small-size;
            margin     : 38px 0 0 0;
            list-style : none outside none;
            position   : absolute;
            right      : 0;
            top        : 0;
            .transition(all 0.25 ease);
            &.show {
              z-index : 12;
              li {
                background-color : #f5f5f5;
                &:hover {
                  box-shadow       : 0 0 20px rgba(0, 0, 0, .2);
                  background-color : #404040;
                  max-width        : 360px;
                  cursor           : pointer;
                  display          : block;
                  white-space      : nowrap;
                  .tag-bar {
                    background-color : #404040;
                  }
                }
                a {
                  color      : #999;
                  opacity    : 1;
                  visibility : visible;
                }
                &:first-child {
                  &:hover {
                    a {
                      color : @white-color;
                    }
                  }
                }
                &:nth-child(odd) {
                  &:hover {
                    a {
                      color      : @white-color;
                      opacity    : 1;
                      visibility : visible;
                    }
                  }
                }
                &:nth-child(even) {
                  &:hover {
                    a {
                      color      : @white-color;
                      opacity    : 1;
                      visibility : visible;
                    }
                  }
                }
              }
            }
            li {
              clear       : both;
              height      : 26px;
              line-height : 26px;
              word-wrap   : normal;
              .transition(all .3s ease);
              .tag-bar {
                float         : right;
                height        : 26px;
                margin-right  : -4px;
                padding-right : 4px;
                width         : 0;
              }
              a {
                color         : #999;
                display       : block;
                float         : right;
                max-width     : 180px;
                overflow      : hidden;
                padding       : 0 10px;
                text-overflow : ellipsis;
                white-space   : nowrap;
                opacity       : 0;
                visibility    : hidden;
                .transition(all .3s ease-in-out);
              }
              &:first-child {
                background-color : #f5f5f5;
                float            : right;
                max-width        : 360px;
                .tag-bar {
                  background-color : #404040 !important;
                }
                a {
                  opacity    : 1;
                  visibility : visible;
                }
              }
              &:nth-child(even) {
                .tag-bar {
                  background-color : #aaa;
                }
              }
              &:nth-child(odd) {
                .tag-bar {
                  background-color : #737373;
                }
              }
            }
          }
        }
      }
      .post-content {
        margin        : 0;
        padding       : 0 20px 15px;
        position      : relative;
        text-overflow : ellipsis;
        z-index       : 11;
        .excerpt {
          color       : #808285;
          font-size   : 13px;
          line-height : 150%;
          a:hover {
            color           : #ba89b6;
            text-decoration : underline;
          }
        }
      }
      .post-thumb {
        margin   : 5px auto 15px;
        overflow : hidden;
        width    : 430px;
        @media only screen and (min-width : 768px) and (max-width : 959px) {
          width : 350px;
        }
        @media only screen and (max-width : 479px) {
          width : 290px;
        }
        img {
          display : block;
          height  : auto;
          opacity : .9;
          width   : 430px;
          @media only screen and (min-width : 768px) and (max-width : 959px) {
            width : 350px;
          }
          @media only screen and (max-width : 479px) {
            width : 290px;
          }
        }
      }
      .post-meta {
        border-top : 1px solid #f2f2f5;
        height     : auto;
        overflow   : hidden;
        margin     : 5px 0 0 0;
        padding    : 5px 0 5px 0;
        & > div {
          border-right : 1px solid #d9d9d9;
          float        : left;
          margin       : 7px 0;
          text-align   : center;
          width        : 33.333%;
          &:last-child {
            border-right : none;
          }
          a {
            color       : #262626;
            display     : block;
            font-size   : @f-normal-size;
            font-weight : bold;
            opacity     : .7;
            .transition(all .3s ease);
            &:hover {
              color   : @brand-primary;
              opacity : 1;
            }
            span {
              font-family : @main-font;
            }
          }
        }
      }
    }
    .list {
      display : none;
    }
  }
}

.kanban {
  backface-visibility         : hidden;
  -webkit-backface-visibility : hidden;
  color                       : #666;
  cursor                      : pointer;
  font-size                   : 20px;
  padding                     : 40px;
  text-transform              : uppercase;
  position                    : relative;
  &:hover {
    &::before {
      opacity : 1;
      .scale(1);
    }
    &::after {
      opacity : 1;
      .scale(1);
    }
    h2, p {
      opacity : 1;
      .translate3d(0, 0, 0);
    }
  }
  &::before,
  &::after {
    position       : absolute;
    top            : 30px;
    right          : 30px;
    bottom         : 30px;
    left           : 30px;
    content        : '';
    opacity        : 0;
    pointer-events : none;
    .transition(all .35s);
  }
  &::before {
    border-top    : 1px solid #666;
    border-bottom : 1px solid #666;
    .scale(0; 1);
  }
  &::after {
    border-right : 1px solid #666;
    border-left  : 1px solid #666;
    .scale(1; 0);
  }
  h2 {
    font-weight : 300;
    padding-top : 10%;
    text-align  : center;
    .transition(transform 0.35s);
    .translate3d(0, -20px, 0);
    span {
      font-weight : 800;
    }
  }
  p {
    color          : #808285;
    font-size      : 68.5%;
    letter-spacing : 1px;
    padding        : 20px 34px;
    opacity        : 0;
    text-align     : center;
    .transition(all 0.35s);
    .translate3d(0, 20px, 0);
  }
}

.i-404 {
  background     : none repeat scroll 0 0 #fcfcfc;
  border-bottom  : 2px solid #ddd;
  float          : left;
  margin         : 0 auto !important;
  overflow       : hidden;
  padding-bottom : 20px;
  width          : 100% !important;
  .wrap-content {
    height   : auto;
    overflow : hidden;
    padding  : 12px 0 0 30px;
    z-index  : 10;
    @media only screen and (min-width : 768px) and (max-width : 959px) {
      padding : 30px 50px 35px;
    }
    @media only screen and (max-width : 767px) {
      padding : 30px 20px 35px
    }
    .not-found {
      background-color : @white-color;
      display          : block;
      width            : 100%;
      img {
        margin         : 0 auto;
        padding-left   : 120px;
        vertical-align : middle;
        @media only screen and (min-width : 768px) and (max-width : 959px) {
          height       : auto;
          padding-left : 0;
          width        : 600px;
        }
        @media only screen and (min-width : 480px) and (max-width : 767px) {
          height       : auto;
          padding-left : 0;
          width        : 398px;
        }
        @media only screen and (max-width : 479px) {
          height       : auto;
          padding-left : 0;
          width        : 258px;
        }
      }
    }
  }
}

.pageload-overlay {
  height     : 100%;
  left       : 0;
  top        : 0;
  width      : 100%;
  visibility : hidden;
  &.show {
    visibility : visible;
  }
  &::after,
  &::before {
    content       : '';
    position      : fixed;
    width         : 40px;
    height        : 40px;
    top           : 50%;
    left          : 50%;
    margin        : -10px 0 0 -10px;
    border-radius : 50%;
    z-index       : 1000;
  }
  &::after {
    background        : #6cc88a;
    -webkit-animation : moveRight 0.8s linear infinite alternate;
    animation         : moveRight 0.8s linear infinite alternate;
    .translateX(-40px);
  }
  &::before {
    background        : #4fc3f7;
    -webkit-animation : moveLeft 0.8s linear infinite alternate;
    animation         : moveLeft 0.8s linear infinite alternate;
    .translateX(40px);
  }
}