* {
  margin: 0;
  padding: 0;
  text-shadow: 0 0 1px rgba(0, 0, 0, 0.1);
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*::before,
*::after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
svg {
  display: none;
}
.cb {
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
}
.fading {
  opacity: 0.1 !important;
}
::selection {
  background: #986794;
  color: #ffffff;
}
::-moz-selection {
  background: #986794;
  color: #ffffff;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}
body {
  background: none repeat scroll 0 0 #eee;
  color: #333;
  font-family: 'Roboto', 'Helvetica Neue', Helvetica, 'Hiragino Sans GB', 'Hiragino Sans GB W3', 'Microsoft YaHei', sans-serif;
  font-size: 12px;
  position: relative;
  margin: 0 auto;
}
body.header-fixed {
  padding-top: 115px;
}
body.header-fixed #menu-mobile {
  top: 0px;
}
body.header-fixed #header {
  position: fixed;
  top: -48px;
  left: 0;
  width: 100%;
  z-index: 999;
}
a {
  color: #333;
  cursor: pointer;
  text-decoration: none;
}
blockquote {
  background: #f9f9f9;
  border-left: 10px solid #ccc;
  margin: 1.5em 10px;
  padding: .5em 10px;
  quotes: "\201C" "\201D" "\2018" "\2019";
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
blockquote p {
  display: inline;
}
blockquote:hover {
  border-left: 10px solid #986794;
}
blockquote:hover::before {
  color: #986794;
}
blockquote::before {
  color: #ccc;
  content: open-quote;
  font-size: 4em;
  line-height: .1em;
  margin-right: .25em;
  vertical-align: -0.4em;
}
pre {
  white-space: pre-wrap;
  white-space: -moz-pre-wrap;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
  word-wrap: break-word;
}
[class^="icon-"] {
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.display-none {
  display: none;
}
.left {
  float: left;
}
.right {
  float: right;
}
.icon-twitter:hover {
  color: #00a0d1;
}
.icon-weibo:hover {
  color: #eb182c;
}
.icon-flickr:hover {
  color: #ff0084;
}
.icon-googleplus:hover {
  color: #db4a39;
}
.icon-instagram:hover {
  color: #634d40;
}
.icon-foursquare:hover {
  color: #25a0ca;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
@keyframes moveRight {
  to {
    -webkit-transform: translateX(40px);
    -moz-transform: translateX(40px);
    -ms-transform: translateX(40px);
    transform: translateX(40px);
  }
}
@keyframes moveLeft {
  to {
    -webkit-transform: translateX(-40px);
    -moz-transform: translateX(-40px);
    -ms-transform: translateX(-40px);
    transform: translateX(-40px);
  }
}
@keyframes heartbeat {
  0% {
    -webkit-transform: scale(0.9);
    -ms-transform: scale(0.9);
    -o-transform: scale(0.9);
    transform: scale(0.9);
  }
  9% {
    -webkit-transform: scale(1.2);
    -ms-transform: scale(1.2);
    -o-transform: scale(1.2);
    transform: scale(1.2);
  }
  18% {
    -webkit-transform: scale(0.9);
    -ms-transform: scale(0.9);
    -o-transform: scale(0.9);
    transform: scale(0.9);
  }
  25% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes ripple-animation {
  from {
    opacity: 0.4;
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
  }
  to {
    opacity: 0;
    -webkit-transform: scale(100);
    -ms-transform: scale(100);
    -o-transform: scale(100);
    transform: scale(100);
  }
}
.share {
  display: none;
  max-width: 600px;
  height: 100px;
  margin: 0 auto;
  -webkit-filter: url("#goo");
  position: relative;
}
@media (min-width: 480px) {
  .share {
    display: block;
  }
}
.share i:hover {
  color: inherit;
}
.share-button,
.share-toggle-button {
  position: absolute;
  background: #b896b5;
  display: inline-block;
  left: 50%;
  top: 50%;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  line-height: 80px;
  margin-left: -40px;
  margin-top: -40px;
  text-align: center;
  color: #ffffff;
  border: none;
  outline: none;
  -webkit-transition: background 0.25s ease;
  -o-transition: background 0.25s ease;
  transition: background 0.25s ease;
}
.share-button:hover,
.share-toggle-button:hover {
  background: #ac84a8;
}
.share-button {
  font-size: 20px;
  color: #ffffff;
  -webkit-transform: scale(0.95, 0.95);
  -ms-transform: scale(0.95, 0.95);
  -o-transform: scale(0.95, 0.95);
  transform: scale(0.95, 0.95);
}
a.share-button {
  color: #ffffff;
}
.share-toggle-button {
  z-index: 9;
  font-size: 30px;
  line-height: 30px;
}
.share-items {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.share-item {
  display: inline-block;
}
.container {
  height: auto;
  overflow: hidden;
  position: relative;
  width: 100%;
}
#post {
  display: block;
  margin: 0 auto;
  width: 1420px;
  height: auto;
  overflow: hidden;
  z-index: 10;
}
@media only screen and (min-width: 960px) and (max-width: 1419px) {
  #post {
    width: 960px;
  }
}
@media only screen and (min-width: 768px) and (max-width: 959px) {
  #post {
    width: 740px;
  }
}
@media only screen and (min-width: 480px) and (max-width: 767px) {
  #post {
    width: 440px;
  }
}
@media only screen and (max-width: 479px) {
  #post {
    width: 300px;
  }
}
#post article {
  position: relative;
}
#post article time {
  display: inline-block;
}
#post article .format-aside .post-title a::before {
  display: inline-block;
  content: '@';
}
.list--view img {
  display: none;
}
.list--view .list--post {
  border-left: 2px solid transparent;
  border-bottom: 1px solid #ddd;
  line-height: 1.71428571;
  padding: 10px 0;
}
.list--view .list--post:last-child {
  border-bottom: 0;
}
.list--view .list--post .wrap-content {
  font-size: 16px;
}
.list--view .list--post .wrap-content .grid {
  display: none;
}
.list--view a.link {
  cursor: pointer;
  color: #424242;
  display: block;
  font-size: 16px;
  padding: 10px 5px;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.list--view a.link:hover {
  padding-left: 15px;
}
.list--view a.link h4,
.list--view a.link time {
  display: inline-block;
  margin: 0 10px 0 0;
  text-transform: none;
}
