.pageload-overlay {
  height: 100%;
  left: 0;
  top: 0;
  width: 100%;
  visibility: hidden;
}
.pageload-overlay.show {
  visibility: visible;
}
.pageload-overlay::after,
.pageload-overlay::before {
  content: '';
  position: fixed;
  width: 40px;
  height: 40px;
  top: 50%;
  left: 50%;
  margin: -10px 0 0 -10px;
  border-radius: 50%;
  z-index: 1000;
}
.pageload-overlay::after {
  background: #6cc88a;
  -webkit-animation: moveRight 0.8s linear infinite alternate;
  animation: moveRight 0.8s linear infinite alternate;
  -webkit-transform: translateX(-40px);
  -moz-transform: translateX(-40px);
  -ms-transform: translateX(-40px);
  transform: translateX(-40px);
}
.pageload-overlay::before {
  background: #4fc3f7;
  -webkit-animation: moveLeft 0.8s linear infinite alternate;
  animation: moveLeft 0.8s linear infinite alternate;
  -webkit-transform: translateX(40px);
  -moz-transform: translateX(40px);
  -ms-transform: translateX(40px);
  transform: translateX(40px);
}
