.ribbon {
  font-size: 16px;
  width: 80%;
  position: relative;
  background: #ba89b6;
  color: #ffffff;
  text-align: center;
  padding: 1em 2em;
  margin: 2em auto 3em;
}
.ribbon ::selection {
  color: #986794;
  background: #ffffff;
}
.ribbon ::-moz-selection {
  color: #986794;
  background: #ffffff;
}
.ribbon .ribbon-content::before {
  content: "";
  position: absolute;
  display: block;
  border-style: solid;
  border-color: #804f7c transparent transparent transparent;
  bottom: -1em;
  left: 0;
  border-width: 1em 0 0 1em;
}
.ribbon .ribbon-content::after {
  content: "";
  position: absolute;
  display: block;
  border-style: solid;
  border-color: #804f7c transparent transparent transparent;
  bottom: -1em;
  right: 0;
  border-width: 1em 1em 0 0;
}
.ribbon::before {
  content: "";
  position: absolute;
  display: block;
  bottom: -1em;
  border: 1.5em solid #986794;
  z-index: -1;
  left: -2em;
  border-right-width: 1.5em;
  border-left-color: transparent;
}
.ribbon::after {
  content: "";
  position: absolute;
  display: block;
  bottom: -1em;
  border: 1.5em solid #986794;
  z-index: -1;
  right: -2em;
  border-left-width: 1.5em;
  border-right-color: transparent;
}
@media only screen and (max-width: 767px) {
  .ribbon {
    background: #ffffff;
    color: #333;
    font-size: 25px;
    margin: 0;
    width: 100%;
    padding: 0;
  }
  .ribbon strong {
    font-weight: normal;
  }
  .ribbon::before,
  .ribbon::after,
  .ribbon .ribbon-content::before,
  .ribbon .ribbon-content::after {
    display: none;
  }
}
