  position: relative;
  list-style: none;
  float: left;
  margin-right: 10px;
  height: 31px;
  overflow: hidden;
}
.box-social .list-follow li a {
  width: 34px;
  height: 31px;
  color: #202121;
  -webkit-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
